<template>
    <div>
        <top-nav title="退款订单"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是所有的待退款及已退款订单</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" size="mini">
                    <el-form-item label="关键字">
                        <el-input placeholder="请输入内容" v-model="search.keywords" class="input-with-select">
                            <el-select style="width: 90px" v-model="search.keywords_type" slot="prepend" placeholder="请选择">
                                <el-option label="订单号" :value="1"></el-option>
                                <el-option label="收货人" :value="2"></el-option>
                            </el-select>
                        </el-input>
                    </el-form-item>

                    <el-form-item label="是否收到货">
                        <el-select v-model="search.goods_status_mean" placeholder="是否收到货">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in datas.goods_status_mean" :label="item.value" :value="item.key" :key="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="退货原因">
                        <el-select v-model="search.reject_reason" placeholder="退货原因">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in datas.goods_reject_reason" :label="item.value" :value="item.key" :key="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="审核状态">
                        <el-select v-model="search.reject_status" placeholder="审核状态">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in datas.order_reject_status" :label="item.value" :value="item.key" :key="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="申请日期">
                        <date-between  v-model="search.date"></date-between>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="mini" type="primary" @click="getOrderList(1)" icon="el-icon-search">搜索</el-button>
                        <el-button size="mini" type="danger" @click="search.keywords='',search.goods_status_mean=-999,search.reject_status=-999,search.reject_status=-999,
                        search.date=[],getOrderList(1)">取消搜索</el-button>
                    </el-form-item>
                </el-form>

                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column width="220" label="订单信息">
                        <template slot-scope="scope">
                            <p>订单ID: {{scope.row.order_id}}&nbsp;&nbsp;
                                <router-link :to="'/order/product/view/'+scope.row.order_id">
                                    <a href="javascript:void(0);" style="color:#409EFF">跟踪</a>
                                </router-link>
                            </p>
                            <p>订单编号: {{scope.row.order_sn}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="用户信息" width="300" >
                        <template slot-scope="scope">
                            <p>昵称：{{scope.row.nickname}} <span>&nbsp;&nbsp;ID: {{scope.row.user_id}}
                                <router-link :to="'/customer/view/'+scope.row.user_id">
                                    <a href="javascript:void(0);" style="color:#409EFF">跟踪</a>
                                </router-link></span>
                            </p>
                            <p>收货人: {{scope.row.name}}</p>
                            <p class="ellipsis-02">收货地址: {{scope.row.address}}</p>
                        </template>
                    </el-table-column>


                    <el-table-column label="退货原因" width="100">
                        <template slot-scope="scope">
                            <div class="ellipsis-03">{{scope.row.reject_reason_mean}}</div>
                        </template>
                    </el-table-column>

                    <el-table-column label="退款信息" width="220">
                        <template slot-scope="scope">
                            <p>订单金额: ￥ {{scope.row.order_price}}</p>
                            <p>收货状态: {{scope.row.reject_goods_status_mean}}</p>
                            <p>退货原因: {{scope.row.reject_reason_mean}}</p>
                            <p>提交时间: {{scope.row.add_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="图片说明" width="260">
                        <template slot-scope="scope">
                            <div class="block" style="display:inline-block;float: left;"
                                 v-for="(item,index) in scope.row.reject_photos">
                                <img v-if="index <= 2" v-bind:src="item" width="50"/> &nbsp;
                            </div>
                            <div v-if="scope.row.reject_photos.length > 3">...</div>
                            <el-button type="text" href="javascript:void(0);" v-if="scope.row.reject_photos.length > 0" @click="photos=scope.row.reject_photos,showP=true">查看</el-button>
                        </template>
                    </el-table-column>


                    <el-table-column label="退款说明" width="200">
                        <template slot-scope="scope">
                            <div class="ellipsis-05">{{scope.row.reject_content}}</div>
                        </template>
                    </el-table-column>

                    <el-table-column label="审核状态" prop="reject_status_mean" width="100"></el-table-column>

                    <el-table-column label="操作 / 状态 / 拒绝原因">
                        <template slot-scope="scope">
                            <div class="list-table-action">
                                <!--<router-link :to="'/order/product/view/'+scope.row.reject_id">-->
                                    <!--<el-button type="text" icon="el-icon-view">查看</el-button>-->
                                <!--</router-link>-->
                                <el-button type="text" @click="agree=true,form=scope.row" v-if="scope.row.reject_status == 0">同意退款</el-button>
                                <el-button type="text" @click="refuse=true,form=scope.row" v-if="scope.row.reject_status == 0">拒绝退款</el-button>

                                <p v-if="scope.row.reject_status == 1">已退款</p>

                                <p v-if="scope.row.reject_status == 2 && scope.row.refuse_reason" class="ellipsis-03" >{{scope.row.refuse_reason}}</p>

                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="getOrderList" :page-size="datas.limit"
                                   layout="total, prev, pager, next" :total="datas.count">
                    </el-pagination>
                </div>
            </div>
        </div>

        <el-dialog :visible.sync="showP" width="500px" :modal-append-to-body="false">
            <el-carousel :interval="5000" arrow="always" >
                <el-carousel-item v-for="item in photos">
                    <img v-bind:src="item"  width="500"/>
                </el-carousel-item>
            </el-carousel>
        </el-dialog>


        <el-dialog title="同意退款" v-loading="subLoading" :visible.sync="agree" width="50%" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small" label-width="150px">
                <el-row :span="24">
                    <el-col :span="6">商品总价 : ￥{{form.goods_price}}</el-col>
                    <el-col :span="6">运费 : ￥{{form.shipping_price}}</el-col>
                    <el-col :span="6">优惠券抵扣 : ￥{{form.coupon_price}}</el-col>
                    <el-col :span="6">其他优惠 : ￥{{form.youhui_price}}</el-col>
                    <el-col :span="6">订单总价 : ￥{{form.order_price}}</el-col>
                    <el-col :span="6">余额支付 : ￥{{form.user_money}}</el-col>
                    <el-col :span="6">在线支付 : ￥{{form.payment_price}}</el-col>
                </el-row>
                <br/>
                <br/>
                <el-form-item label="是否回退优惠券" >
                    <el-switch v-model="form.is_coupon" active-value="1" inactive-value="0"></el-switch>
                </el-form-item>
                <el-form-item label="是否回退用户余额" >
                    <el-switch v-model="form.is_usermoney" active-value="1" inactive-value="0"></el-switch>
                </el-form-item>
                <el-form-item label="回退用户余额金额" v-if="form.is_usermoney == 1">
                    <el-input v-model="form.usermoney" placeholder="回退用户余额,不得大于支付时使用的用户余额"></el-input>
                </el-form-item>
                <el-form-item label="是否回退在线支付" >
                    <el-switch v-model="form.is_payment_price" active-value="1" inactive-value="0"></el-switch>
                </el-form-item>
                <el-form-item label="回退在线支付金额" v-if="form.is_payment_price == 1">
                    <el-input v-model="form.payment_price" placeholder="回退用户余额,不得大于支付时使用的用户余额"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="agree = false">取 消</el-button>
                <el-button size="small" type="primary" @click="agreeRefund(true)">确 定</el-button>
            </div>
        </el-dialog>


        <el-dialog title="拒绝退款" v-loading="subLoading" :visible.sync="refuse" width="50%" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small">
                <el-form-item label="拒绝原因" label-width="80px">
                    <el-input type="textarea" :rows="3" placeholder="请输入拒绝原因" v-model="form.refuse_reason"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="refuse = false">取 消</el-button>
                <el-button size="small" type="primary" @click="refuseRefund()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import DateBetween from "../../../components/DateBetween";
    export default {
        name: "customerIndex",
        components: {DateBetween, TopNav},
        data: function () {
            return {
                //整体菊花
                select:1,
                loading:false,
                subLoading: false,
                showP: false,
                agree: false,
                refuse: false,
                search:{
                    date:[],
                    keywords:'',
                    keywords_type:1,
                    pay_status:0,
                    pay_type:0,
                    reject_goods_status:-999,
                    goods_status_mean:-999,
                    order_status:-999,
                    reject_reason:-999,
                    prom_type:0,
                    device:0,
                },
                form: {
                    refuse_reason: '',
                    is_coupon: 0,
                    is_usermoney: 0,
                    usermoney: 0,
                    is_payment_price: 0,
                    payment_price: 0,
                },
                datas: {
                    list: [],
                    goods_reject_reason: {},
                    order_reject_status: {},
                    goods_status_mean: {},
                },
                photos: [],
                status:{},
                userList:[],
            };
        },
        created() {
            this.getOrderList();
        },
        methods: {
            //获取订单列表
            getOrderList(page=1){
                this.loading = true;
                api.post("/shop/admin.order.goodsorder/refundOrder", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                    end_add_time:this.search.date[1] ? this.search.date[1] :  0,
                    bg_add_time:this.search.date[0] ? this.search.date[0] : 0,
                    keywords:this.search.keywords,
                    keywords_type:this.search.keywords_type,
                    reject_reason:this.search.reject_reason,
                    shipping_status:this.search.shipping_status,
                    goods_status_mean:this.search.goods_status_mean,
                    order_status:this.search.order_status,
                    prom_type:this.search.prom_type,
                    device:this.search.device,
                }).then(res => {
                    this.loading = false;
                    this.datas = res;

                    if(page <= 1){
                        this.status = res.status;
                    }
                }).catch(() => {
                    this.loading = false;
                });
            },


            //同意退款
            agreeRefund() {
                this.subLoading = true;
                api.post("/shop/admin.order.goodsorder/agreeRefund", {
                    appToken: this.$store.getters.appToken,
                    reject_id: this.form.reject_id,
                    is_coupon: this.form.is_coupon,
                    is_usermoney: this.form.is_usermoney,
                    usermoney: this.form.usermoney,
                    is_payment_price: this.form.is_payment_price,
                    payment_price: this.form.payment_price,
                }).then(res => {
                    this.datas = res;
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.agree = false;
                    this.getOrderList();
                }).catch(() => {
                    this.subLoading = false;
                });
            },


            refuseRefund() {
                this.subLoading = true;
                api.post("/shop/admin.order.goodsorder/refuseRefund", {
                    appToken: this.$store.getters.appToken,
                    refuse_reason: this.form.refuse_reason,
                    reject_id: this.form.reject_id,
                }).then(() => {
                    this.$message({
                        message: '操作成功',
                        type: 'success',
                    });
                    this.subLoading = false;
                    this.refuse = false;
                    this.getOrderList();
                }).catch(() => {
                    this.subLoading = false;
                    this.refuse = false;
                });
            },

        }
    };
</script>
